// main page elements
body {
	font-size: 14px;
	color: white;
	font-family: 'Rubik', sans-serif;
	font-weight: 400;
	background: white;
	overflow-x: hidden;
	background: #000205;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

a {
	color: white; }

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	position: relative;
	max-width: 1440px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1440px) {
		padding: 0 30px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 15px; } }

.flex {
	display: flex; }

.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	border-radius: 12px;
	color: white; }


//Index-page Top-slider
.main-slider {
	display: flex;
	position: relative;
	padding-top: 23px;
	justify-content: space-between;
	@include mq($max-width: 1279px) {
		padding-top: 0; }
	&__left {
		position: relative;
		max-width: 490px;
		width: 100%;
		height: 682px;
		border-radius: 40px;
		z-index: 10;
		@include mq($max-width: 1366px) {
			height: 618px;
			max-width: 577px; }
		@include mq($max-width: 1365px) {
			height: auto;
			max-width: 550px; }
		@include mq($max-width: 1279px) {
			display: none; }
		img {
			@include mq($max-width: 1366px) {
				max-width: 577px;
				width: 100%; } }

		h2 {
			position: absolute;
			bottom: 38px;
			left: 66px;
			font-weight: 900;
			font-size: 66px;
			line-height: 92.69%;
			@include mq($max-width: 1366px) {
				left: 59px;
				bottom: 34px;
				font-size: 59.7453px;
				line-height: 92.69%; } } }
	&__absolute {
		position: absolute;
		top: -23px;
		left: -88.89px;
		z-index: 2;
		@include mq($max-width: 1366px) {
			left: -75.89px; } }
	&__price {
		position: absolute;
		bottom: 55px;
		left: 188px;
		@include mq($max-width: 1279px) {
			left: 38px;
			bottom: 80px; }
		@include mq($max-width: 1023px) {
			bottom: 30px; }
		@include mq($max-width: 767px) {
			bottom: 15px;
			left: 15px; }
		@include mq($max-width: 374px) {
			padding-right: 30px;
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: 100%; }

		&-main {
			display: flex;
			align-items: center;
			justify-content: space-between;
			gap: 30px;
			margin-bottom: 6px;
			@include mq($max-width: 767px) {
				gap: 10px;
				margin-bottom: 0; }


			.main-slider__price-txt {
				font-weight: 700;
				font-size: 55px;
				line-height: 110.19%;
				@include mq($max-width: 767px) {
					font-size: 36px;
					line-height: 100%; }
				@include mq($max-width: 413px) {
					font-size: 26px; } }
			.main-slider__price-sell {
				display: flex;
				align-items: center;
				justify-content: center;
				background: linear-gradient(108.47deg, #F59502 -0.66%, #F55902 106.05%);
				border-radius: 10px;
				min-width: 67px;
				height: 37px;
				font-weight: 500;
				font-size: 16px;
				line-height: 125.69%; } }

		&-old {
			font-weight: 400;
			font-size: 47.9141px;
			line-height: 110.19%;
			color: rgba(255, 255, 255, 0.8);
			text-decoration: line-through;
			@include mq($max-width: 767px) {
				font-size: 36px;
				line-height: 100%; }
			@include mq($max-width: 413px) {
				font-size: 26px; } } }
	&__right {
		max-width: 885px;
		width: 100%;
		@include mq($max-width: 1440px) {
			max-width: 830px; }
		@include mq($max-width: 1366px) {
			max-width: 815px;
			position: absolute;
			right: 0; }
		@include mq($max-width: 1365px) {
			max-width: 730px; }
		@include mq($max-width: 1279px) {
			position: relative;
			max-width: 100%; }

		.mySwiper {
			overflow: hidden;
			@include mq($max-width: 1279px) {
				padding-bottom: 40px; }
