.swiper-pagination {
	display: none;
	@include mq($max-width: 1279px) {
		display: block; } }
.swiper-pagination-bullet {
	width: 13px;
	height: 13px;
	margin: 0 5px;
	background: rgba(255, 255, 255, 0.1);
	opacity: 1; }
.swiper-pagination-bullet-active {
	opacity: 1;
	background: rgba(255, 255, 255, 0.6); }

